@import 'variables.scss';

.layout {
  min-height: 100%;
  min-width: 320px;
  margin: 0 auto;
  padding: 0;
  font-family: 'Open Sans', 'Arial', sans-serif;
  font-size: 16px;
  line-height: 32px;
  background-color: $layout-background-color;

  &__header {
    position: relative;
    max-width: 875px;
    margin: 0 auto -63px;
  }
}


.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
